.App {
  text-align: center;
  position: relative;
  min-height: 100vh;
  font-family: 'Times New Roman', Times, serif;
}

#pg-content {
  padding-bottom: 5rem;
}

#footer {
  background: linear-gradient(35deg, black 10%, turquoise 90%);
  position: absolute;
  justify-content: space-between;
  bottom: 0;
  width: 100%;
  height: 5rem;
  padding-bottom: 1rem;
  font-size: 1.5rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
